<template>
  <div>
    <div class="m10">
      <van-cell center
                title="房间数量"
                :label="$route.query.round == '0'?'':`${u_price}元/间/月`">
        <van-stepper v-if="$route.query.round == '0'"
                     :min="realRooms"
                     :max="$route.query.roomCap"
                     :disable-input='true'
                     @minus='minus'
                     v-model="rooms" />
        <van-stepper v-else-if="$route.query.round == '1'"
                     :min="$route.query.roomCap"
                     :disable-input='true'
                     @minus='minus'
                     v-model="rooms" />
      </van-cell>
      <div v-if="$route.query.round == '1'"
           class="addContent">新增{{ rooms - $route.query.roomCap }}间，共{{ rooms }}间</div>
      <div v-if="$route.query.round == '1'"
           class="flex round">
        <div>购买周期</div>
        <div>{{ sTime }}至{{ $route.query.endLineTime }}</div>
      </div>
      <div v-if="$route.query.round == '0'"
           class="round">
        <div>购买时长</div>
        <div v-if="periodMonth"
             class="c_b2">{{ sTime | dataFilter('YYYY年MM月DD日') }}至{{ eTime | dataFilter('YYYY年MM月DD日') }}到期</div>
        <div v-else
             class="c_b2">{{ sTime | dataFilter('YYYY年MM月DD日') }}到期</div>
        <div class="flex time_price_all">
          <div v-for="(item,i) in timeAll"
               :key="i"
               class="time_price"
               id="colorBox"
               :class="chooseIndex == i?'time_price_active':''"
               @click="choose(i,item)">
            <div>
              <div>{{ item.periodMonth }}个月</div>
              <div class="c_60">{{ item.amount }}元/间</div>
            </div>
          </div>
        </div>
      </div>
      <div class="flex bottom_box">
        <div class="w60">
          <span class="c_60"
                v-if="$route.query.round == '0'">¥{{ totlePrice }}</span>
          <span class="c_60"
                v-if="$route.query.round == '1'">¥{{ totlePriceAll }}</span>
          <span class="c_b2"
                v-if="$route.query.round == '1' && $route.query.charge == '1'">{{ rooms - $route.query.roomCap }}间*{{ unitPrice }}/间</span>
        </div>
        <div class="w40 pay"
             @click="goNext('/continuePay-page')">立即付款</div>
      </div>
    </div>
  </div>
</template>
<script>
import { PostBusinessHotelSoftCostByhotel, GetBusinessRoomNumByhotel, PostBusinessHotelSoftCostBillRenew, PostBusinessHotelSoftCostBillExp } from '@/api/api_soft';
import { Toast } from 'vant';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            realRooms: 1,
            bbb: false,
            sTime: null,
            eTime: null,
            timeAll: [],
            totlePrice: 0,
            rooms: null,
            periodMonth: null,
            u_price: null,
            unitPrice: null,
            chooseIndex: -1
        };
    },
    created() {
        this.getRooms();
        this.rooms = this.$route.query.roomCap;
        this.sTime = dayjs().format('YYYY-MM-DD');
        this.$store.commit('setHeader', `${this.$route.query.round}` == '0' ? '套餐选择' : '增加房间');
    },
    computed: {
        totlePriceAll() {
            if (this.$route.query.charge == '0') {
                return 0;
            } else {
                if (this.$route.query.round == '0') {
                    return (this.rooms * this.totlePrice).toFixed(2);
                } else {
                    return (this.unitPrice * this.rooms).toFixed(2);
                }
            }
        }
    },
    methods: {
        // 查询酒店房间数量
        getRooms() {
            GetBusinessRoomNumByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.realRooms = res.data;
                this.getInfo();
            });
        },
        // 获取酒店软件收费标准
        getInfo() {
            PostBusinessHotelSoftCostByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                this.timeAll = res.data;
                if (this.$route.query.round == '1') {
                    this.u_price = this.timeAll[0].amount;
                    this.unitPrice = (this.timeAll[0].amount / (this.timeAll[0].periodMonth * 30)).toFixed(2);
                } else {
                    this.unitPrice = this.timeAll[0].amount.toFixed(2);
                }
            });
        },
        // 选择周期时长
        choose(i, item) {
            this.periodMonth = item.periodMonth;
            this.totlePrice = item.amount;
            this.eTime = dayjs().add(item.periodMonth, 'month').format('YYYY-MM-DD');
            this.chooseIndex = i;
        },
        // 减少房间数量
        minus() {
            if (this.$route.query.roomCap - 1 == this.realRooms) {
                Toast({
                    message: '房间数量不可少于当前系统房间数量',
                    duration: 1000
                });
                return false;
            } else {
                this.bbb = false;
            }
        },
        // 增加房间数量
        plus() {
            if (this.$route.query.roomCap - 1 == this.rooms) {
                Toast({
                    message: '啦啦啦',
                    duration: 1000
                });
                this.bbb = true;
            }
        },
        goNext(url) {
            if (this.$route.query.round == '0' && !this.periodMonth) {
                Toast('请选择购买时长');
                return false;
            }
            let data = {};
            if (this.$route.query.round == '0') {
                data = {
                    hotelId: this.$route.query.hotelId,
                    periodMonth: this.periodMonth,
                    roomCap: this.rooms,
                    totalPrice: this.totlePriceAll
                };
                PostBusinessHotelSoftCostBillRenew(data).done((res) => {
                    if (data.totalPrice == '0') {
                        Toast({
                            message: res.msg,
                            duration: 900
                        });
                        setTimeout('window.history.go(-1)', 900);
                    } else {
                        this.$router.push({
                            path: url,
                            query: {
                                code: res.data,
                                hotelSoftCostBillId: res.order.id
                            }
                        });
                    }
                });
            } else {
                data = {
                    hotelId: this.$route.query.hotelId,
                    roomCap: this.rooms - this.$route.query.roomCap,
                    totalPrice: this.$route.query.charge == '0' ? '0' : this.totlePriceAll
                };
                PostBusinessHotelSoftCostBillExp(data).done((res) => {
                    if (data.totalPrice == '0') {
                        Toast({
                            message: res.msg,
                            duration: 900
                        });
                        setTimeout('window.history.go(-1)', 900);
                    } else {
                        this.$router.push({
                            path: url,
                            query: {
                                code: res.data,
                                hotelSoftCostBillId: res.order.id
                            }
                        });
                    }
                });
            }
        }
    }
};
</script>
<style scoped>
.addContent {
    background: #fff;
    font-size: 13px;
    text-align: right;
    padding: 0 16px 10px;
}
.round {
    background: #fff;
    padding: 10px 16px;
    font-size: 14px;
    color: #000;
    justify-content: space-between;
    margin-top: 10px;
}
.time_price_all {
    width: 100%;
    flex-wrap: wrap;
    margin-top: 10px;
}
.time_price {
    border: 1px solid #dad7db;
    width: 22%;
    padding: 22px 0;
    text-align: center;
    margin: 4px;
    border-radius: 5px;
}
.flex {
    display: flex;
}
.m10 {
    margin: 10px 0;
    text-align: left;
}
.c_b2 {
    color: #b2b2b2;
    font-size: 12px;
}
.c_60 {
    color: #fd7b40;
}
.bottom_box {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 14px;
    color: #000;
    line-height: 40px;
    text-align: center;
}
.w60 {
    width: 60%;
    background: #fff;
}
.w40 {
    width: 40%;
}
.pay {
    background: #fd7b40;
    color: #fff;
}
.time_price_active {
    background-color: #ffeee8;
    border: 1px solid #fd7b40;
}
/deep/ .van-cell__title {
    text-align: left;
}
/deep/ .van-cell {
    font-size: 14px;
    color: #000;
}
/deep/ .van-cell__label {
    color: #ff7c40;
}
/deep/ .van- :min='5'__plus {
    color: #ff7c40;
}
</style>



